body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}
.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 4vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin: 0; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.nav {
  background-color: rgba(255, 255, 255, 0.2);
  background-clip: content-box; }

/* // export default; */

.home {
  background-image: url("/images/1(451).jpg");
  background-position: center calc(100% + 200px); }
  .home .pic {
    padding: 0;
    max-height: 100 vh; }

.button {
  margin: 25px;
  padding: 16px; }

.gallery {
  background-color: cornsilk; }
  .gallery .images {
    width: 540px;
    height: 360px; }

.time {
  height: 50%;
  padding-top: 50px;
  padding-bottom: 50px; }
  .time #countdown {
    border-top: 3px solid #dc3545;
    border-bottom: 3px solid #dc3545; }
    .time #countdown .clock {
      width: 135px; }
      .time #countdown .clock .days, .time #countdown .clock .hours, .time #countdown .clock .minutes, .time #countdown .clock .seconds {
        font-size: 3rem; }

.wishes {
  border: 3px solid firebrick;
  border-radius: 10px; }

.gradient {
  background: #f6f6f6;
  background: -webkit-gradient(left top, right top, color-stop(0%, #f6f6f6), color-stop(50%, white), color-stop(100%, #ededed));
  background: -webkit-linear-gradient(left, #f6f6f6 0%, white 50%, #ededed 100%);
  background: -webkit-gradient(linear, left top, right top, from(#f6f6f6), color-stop(50%, white), to(#ededed));
  background: linear-gradient(to right, #f6f6f6 0%, white 50%, #ededed 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f6f6f6', endColorstr='#ededed', GradientType=1 ); }
  .gradient .form-container {
    padding: 50px 0px; }
    .gradient .form-container .form-control {
      border: 1px solid #dc3545; }
    .gradient .form-container .input-group-text {
      border: 1px solid #dc3545;
      background-color: #17a2b8;
      color: #fff; }
    .gradient .form-container textarea {
      height: 100px; }

.footer {
  background-color: dimgrey; }

